*,
*::before,
*::after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    min-height: 100vh;

    display: flex;
    flex-direction: column;
    overflow-x: hidden;
}

.body {
      font-family: Roboto, sans-serif;
      letter-spacing: 0.03em;
      color: #757575;
      background-color: #ffffff;
  }

link {
  text-decoration: none;
}

.list {
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

a {
  text-decoration: none;
  color: currentColor;
}

ul {
  list-style: none;
  // padding: 0;
  // margin: 0;
}
